import React from "react";
import { NavLink } from "react-router-dom";
import "./Profile.css";
export const Profile = ({ search, setSearch }) => {
  const SearchShow = () => {
    setSearch(!search);
    console.log(search);
  };
  return (
    <>
      <ul className="navbarProfile">
        <li className="nav-item">
          <NavLink to="/" className="nav-item-content">
            <img
              src="data:image/png;base64,"
              alt="en"
              width="22"
              height="14"
            />
            <span>English</span>
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink to="/" className="nav-item-content">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="21px"
              height="21px"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="feather feather-moon"
            >
              <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path>
            </svg>
          </NavLink>
        </li>
        <li className="nav-item" id="searchb">
          <NavLink to="" className="nav-item-content">
            <svg
              onClick={SearchShow}
              data-v-0e8a7f4f=""
              xmlns="http://www.w3.org/2000/svg"
              width="21px"
              height="21px"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="feather feather-search"
            >
              <circle data-v-0e8a7f4f="" cx="11" cy="11" r="8"></circle>
              <line
                data-v-0e8a7f4f=""
                x1="21"
                y1="21"
                x2="16.65"
                y2="16.65"
              ></line>
            </svg>
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink to="/" className="nav-item-content float-count">
            <svg
              data-v-a58fee00=""
              xmlns="http://www.w3.org/2000/svg"
              width="21px"
              height="21px"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="text-body feather feather-shopping-cart"
            >
              <circle data-v-a58fee00="" cx="9" cy="21" r="1"></circle>
              <circle data-v-a58fee00="" cx="20" cy="21" r="1"></circle>
              <path
                data-v-a58fee00=""
                d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"
              ></path>
            </svg>
            <span className="count">5</span>
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink to="/" className="nav-item-content float-count">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="21px"
              height="21px"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="text-body feather feather-bell"
            >
              <path d="M18 8A6 6 0 0 0 6 8c0 7-3 9-3 9h18s-3-2-3-9"></path>
              <path d="M13.73 21a2 2 0 0 1-3.46 0"></path>
            </svg>
            <span className="notfiy-count">6</span>
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink to="/" className="nav-item-content user">
            <div className="profile">
              <span className="name">
                John Doe
                <span className="role">admin</span>
              </span>
            </div>
            <div className="profile-img">
              <img
                className="Profilephoto"
                src="https://pixinvent.com/demo/vuexy-vuejs-admin-dashboard-template/demo-1/img/13-small.d796bffd.png"
                alt="avatar"
              />
              <span className="online"></span>
            </div>
          </NavLink>
        </li>
      </ul>
    </>
  );
};
export default Profile;
